:root {
  --fastai-blue: #347DBE;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

body {
  height: 100%;
  background-color: #347DBE;
  background-color: var(--fastai-blue);
}

a {
  color: inherit;
}

.center {
  display: flex;
  flex-direction: column;
  flex: 6 1;
  background-color: white;
}

.center .chapter-nav {
  background-color: var(--fastai-blue);
  flex: 1 1;
  display: flex;
  flex-direction: column;
}

.center .chapter-nav .chapter {
  flex: 1 1;
  text-align: center;
}

.row {
  display: flex;
  flex-direction: row;
}

.left {
  background-color: var(--fastai-blue);
  display: flex;
  position: relative;
  text-align: right;
  flex-direction: column;
  flex: 2 1;
  width: 10rem;;
  max-width: 10rem;
  padding: 0 1%;
}

.left.closed {
  background-color: var(--fastai-blue);
  display: flex;
  flex-direction: column;
  flex: 0 1;
  padding: 0;
}

.chapter-nav .chapter {
  cursor: pointer;
}

.TranscriptBrowser .top {
  display: flex;
  position: absolute;
  bottom: 0;
  height: 5vh;
  flex-direction: row;
  justify-content: space-between;
  overflow-x: scroll;
  overflow-y: hidden;
  max-width: 100%;
}

.TranscriptBrowser .top .search-result {
  cursor: pointer;
  padding: 0 2% 0 0;
  width: 10vw;
}

.TranscriptBrowser .top .search-result:hover {
  text-decoration: underline;
}

.TranscriptBrowser span {
  flex: 1 1;
  opacity: 0.5;
  margin: auto;
  text-align: center;
}

.Moment {
  padding: 1rem;
  margin: auto;
  font-size: 1.5rem;
}

